/* global jQuery TimelineLite Waypoint MktoForms2*/


/*
 * animate heading and computers
 */
(function () {

	"use strict";

	var headingAnimated = false;
	var ease = window.Power4.easeInOut;

	new Waypoint({
		element: document.getElementById('qitam'),
		offset: "80%",
		handler: function(direction) {
			var t1 = new TimelineLite();
			if (direction === "down" && !headingAnimated) {
				t1.fromTo(".intro-container .heading--2", 1, { y: "-10%", autoAlpha: 0 }, { y: "0%", autoAlpha: 1, ease: ease });
				t1.fromTo(".intro-container .paragraph--1", 1, { y: "10%", autoAlpha: 0 }, { y: "0%", autoAlpha: 1, ease: ease });
			}

			headingAnimated = true;
		}
	});

	var computersDesktopAnimated = false;

	new Waypoint({
		element: document.getElementById('computers'),
		offset: "80%",
		handler: function(direction) {
			var t0 = new TimelineLite();
			var t1 = new TimelineLite({onComplete:function() {
					this.restart();
				}
			});

			if (direction === "down" && !computersDesktopAnimated) {
				t0.fromTo(".screenshot.desktop", 1.5, { x: "25%", autoAlpha: 0 }, { x: "0%", autoAlpha: 1, ease: ease });
				// screenshot 1
				t1.to(".screen1", 1, { top: "-58.4%", ease: ease }, "3.5"); // at t=3.5s, slide up
				t1.to(".screen1", 1, { top: "-147.4%", ease: ease }, "+=2"); // 2s later, slide up
				t1.to(".screen1", 1, { top: "-236.4%", ease: ease }, "+=2"); // 2s later, slide up
				t1.to(".screen1", 1, { autoAlpha: 0, ease: ease }, "+=2"); // 2s later, fade out
				t1.to(".screen1", 0, { top: "10.6%", ease: ease }, "+=1"); // 1s later, reset top position of screenshot 1 immediately
				// screenshot 2
				t1.to(".screen2", 1, { autoAlpha: 1, ease: ease }, "-=2"); // fade with previous animation to get cross fade
				t1.to(".screen2", 1, { top: "-24.4%", ease: ease }, "+=2"); // 2s later, slide up
				t1.to(".screen2", 1, { autoAlpha: 0, ease: ease }, "+=2"); // 2s later, fade out
				// screenshot 3
				t1.to(".screen3", 1, { autoAlpha: 1, ease: ease }, "-=1"); // fade with previous animation to get cross fade
				t1.to(".screen3", 1, { top: "-30.4%", ease: ease }, "+=2"); // 2s later, slide up
				t1.to(".screen3", 1, { autoAlpha: 0, ease: ease }, "+=2"); // 2s later, fade out
				// screenshot 4
				t1.to(".screen4", 1, { autoAlpha: 1, ease: ease }, "-=1"); // fade with previous animation to get cross fade
				t1.to(".screen4", 1, { top: "-62.4%", ease: ease }, "+=2"); // 2s later, slide up
				t1.to(".screen4", 1, { autoAlpha: 0, ease: ease }, "+=2"); // 2s later, fade out
				// screenshot 5
				t1.to(".screen5", 1, { autoAlpha: 1, ease: ease }, "-=1"); // fade with previous animation to get cross fade
				t1.to(".screen5", 1, { top: "-61.4%", ease: ease }, "+=2"); // 2s later, slide up
				t1.to(".screen5", 1, { autoAlpha: 0, ease: ease }, "+=2"); // 2s later, fade out
				// screenshot 6
				t1.to(".screen6", 1, { autoAlpha: 1, ease: ease }, "-=1"); // fade with previous animation to get cross fade
				t1.to(".screen6", 1, { top: "-22.4%", ease: ease }, "+=2"); // 2s later, slide up
				t1.to(".screen6", 1, { autoAlpha: 0, ease: ease }, "+=2"); // 2s later, fade out
				// screenshot 7
				t1.to(".screen7", 1, { autoAlpha: 1, ease: ease }, "-=1"); // fade with previous animation to get cross fade
				t1.to(".screen7", 1, { autoAlpha: 0, ease: ease }, "+=2"); // 2s later, fade out
				// screenshot 8
				t1.to(".screen8", 1, { autoAlpha: 1, ease: ease }, "-=1"); // fade with previous animation to get cross fade
				t1.to(".screen8", 1, { autoAlpha: 0, ease: ease }, "+=2"); // 2s later, fade out
				// screenshot 1
				t1.to(".screen1", 1, { autoAlpha: 1, ease: ease }, "-=1"); // fade with previous animation to get cross fade
			}

			computersDesktopAnimated = true;
		}
	});

}());


/*
 * populate "position" field
 */
(function ($,MktoForms2) {

	"use strict";

	MktoForms2.whenReady(function (form) {
		form.getFormElem().find("[name=formPosition]").val("top");
	});

}(jQuery,MktoForms2));